
#app {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* height: 100vh; */
  padding: 0px;
  background: url(data:image/svg+xml;base64,) no-repeat center center fixed;
  background-size: cover;
}
body{
  background: linear-gradient(10deg,rgb(179, 233, 255), rgb(255, 137, 107)) no-repeat center center fixed;
  background-size: cover;
  margin: 0px;
  padding: 0px;
  height: 100vh;
}
h1{
  color:white;
  font-weight: 800;
}

.header-button{
  background: rgb(0, 255, 183);
  border-radius: 100px;
  color:white;
  border:none;
  font-weight: 600;
  padding:1rem;
  font-size: 1.3rem;
}
.logo{
  width: 10rem;
}
.header{
  padding-top:2rem;
  margin:0px;
}

.hunk-white{
  height: 500px;
  background-color:rgba(255, 255, 255, 0.877);
  width:100vw;
  padding: 1rem 0;
  margin-top: 1rem;
}
.hunk-clear{
  height: 500px;
  background-color:none;
  width:100vw;
  padding: 1rem 0;
  margin-top: 1rem;
}

